'use client';

import { FormField } from '@/components/ui/form-field';

interface PlanogramNameFieldProps {
  value: string;
  onChange: (value: string) => void;
}

export function PlanogramNameField({ value, onChange }: PlanogramNameFieldProps) {
  return (
    <FormField
      label='Name'
      value={value}
      onChange={(e) => onChange(e.target.value)}
      placeholder='Planogram name'
      required
      containerClassName='lg:col-span-2'
    />
  );
}

