'use client';

import { Button } from '@/components/ui/button';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { usePlanogramStore } from '@/features/planogram';
import { AIOverviewDialog } from '../ai-overview-dialog/ai-overview-dialog';

interface PlanogramActionsProps {
  name: string;
  onRegenerate: () => void;
  isRegenerating: boolean;
  error?: Error | null;
}

export function PlanogramActions({ name, onRegenerate, isRegenerating, error }: PlanogramActionsProps) {
  const sidebarOpen = usePlanogramStore.use.sidebarOpen();
  const toggleSidebar = usePlanogramStore.use.toggleSidebar();

  return (
    <>
      <div className='mt-4 pt-4 border-t flex gap-4'>
        <Button
          onClick={onRegenerate}
          disabled={isRegenerating || !name?.trim()}
          className='w-full md:w-auto cursor-pointer'
        >
          {isRegenerating ? 'Updating...' : 'Regenerate'}
        </Button>
        <Button onClick={toggleSidebar} variant='outline' className='w-full md:w-auto cursor-pointer'>
          {sidebarOpen ? 'Hide Products' : 'Explore Products'}
        </Button>
      </div>

      {error && (
        <Alert variant='destructive' className='mt-4'>
          <AlertDescription>{error.message}</AlertDescription>
        </Alert>
      )}

      <AIOverviewDialog />
    </>
  );
}

