'use client';

import { useParams } from 'next/navigation';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { X, ChevronLeft, ChevronRight, Minus, Plus } from 'lucide-react';
import { usePlanogramStore, usePlanogramData } from '@/features/planogram';
import { useAvailableProductsQuery } from '@/features/planogram/queries';
import type { LayoutItem } from '@/features/planogram/types';

// Helper function to calculate width in cells (matches backend logic)
function widthCellsFor(widthIn: number, cellWidthIn: number): number {
  if (widthIn <= 0) {
    return 0;
  }
  if (widthIn < 12) {
    return 1;
  }
  // For 12+ inches, use floor division (12" = 2 cells, 18" = 3 cells, etc.)
  return Math.max(2, Math.floor(widthIn / cellWidthIn));
}

export function AvailableProductsSidebar() {
  const params = useParams();
  const planogramSlug = params?.planogramSlug as string;
  
  // Get planogram data for categories and season
  const { planogramData } = usePlanogramData(planogramSlug);
  const planogram = planogramData?.planogram;
  
  // Fetch available products based on planogram categories/season
  const categoryIds = (planogram?.category_ids ?? []) as number[];
  const season = planogram?.season ?? 'summer';
  
  const { data: availableItems = [], isLoading: loadingAvailableItems } = useAvailableProductsQuery({
    categoryIds,
    season,
  });
  
  // Available Products slice state
  const selectedAvailableItems = usePlanogramStore.use.selectedItems();
  const incrementItem = usePlanogramStore.use.incrementItem();
  const decrementItem = usePlanogramStore.use.decrementItem();
  const closeAvailableProducts = usePlanogramStore.use.closeAvailableProducts();
  const targetRowId = usePlanogramStore.use.targetRowId();
  const setTargetRowId = usePlanogramStore.use.setTargetRowId();
  const availableProductsSidebarExpanded = usePlanogramStore.use.availableProductsSidebarExpanded();
  const toggleAvailableProductsSidebarExpand = usePlanogramStore.use.toggleAvailableProductsSidebarExpand();
  
  // Grid slice state
  const gridData = usePlanogramStore.use.gridData();
  const rowLayouts = usePlanogramStore.use.rowLayouts();
  const setRowLayouts = usePlanogramStore.use.setRowLayouts();
  const clearSelectedItems = usePlanogramStore.use.clearSelectedItems();

  // Handle adding selected items to a row
  const handleAddSelectedItems = () => {
    if (!gridData || targetRowId === null || selectedAvailableItems.size === 0) {
      return;
    }

    const maxCols = gridData.grid.cols;
    const newItems: LayoutItem[] = [];

    // Loop through each selected item and its quantity
    selectedAvailableItems.forEach((quantity, itemId) => {
      const item = availableItems.find((i) => i.id === itemId);
      if (!item) {
        return;
      }

      // Add the item 'quantity' number of times
      for (let i = 0; i < quantity; i++) {
        const itemWidth = widthCellsFor(item.pack_width_in, gridData.grid.cellWidthIn);

        // Get the target row
        const targetRow = gridData.rows.find((r) => r.id === targetRowId);
        if (targetRow) {
          const rowLayout = rowLayouts[targetRowId] && rowLayouts[targetRowId].length > 0 ? rowLayouts[targetRowId] : targetRow.items;

          // Combine existing items with items we're adding in this batch (for collision detection)
          const itemsInThisRow = newItems.filter((item) => item.y + 1 === targetRowId);
          const allItemsInRow = [...rowLayout, ...itemsInThisRow];

          // Try to find a position from left to right, starting from x=0
          let finalX = 0;
          let placed = false;
          let testX = 0;
          
          while (testX + itemWidth <= maxCols) {
            // Check for collisions with existing items and items we're adding
            const hasCollision = allItemsInRow.some((existingItem) => {
              const existingRight = existingItem.x + existingItem.w;
              const itemRight = testX + itemWidth;
              return (testX >= existingItem.x && testX < existingRight) || (itemRight > existingItem.x && itemRight <= existingRight) || (testX < existingItem.x && itemRight > existingRight);
            });

            if (!hasCollision) {
              // Found a good position!
              finalX = testX;
              placed = true;
              break;
            }

            testX++;
          }

          // If no perfect position found in target row, place at the rightmost position (allow overflow)
          if (!placed) {
            const rightmostX = allItemsInRow.length > 0 ? Math.max(...allItemsInRow.map((item) => item.x + item.w)) : 0;
            finalX = rightmostX;
          }

          // Use timestamp-based unique ID to avoid duplicates
          const timestamp = Date.now();
          const randomSuffix = Math.random().toString(36).substring(2, 9);
          const uniqueId = `${item.id}-${timestamp}-${randomSuffix}`;

          newItems.push({
            i: uniqueId,
            x: finalX,
            y: targetRowId - 1,
            w: itemWidth,
            h: 1,
            meta: {
              id: item.id,
              name: item.name,
              category: item.category,
              color: item.color || '#9ca3af',
              score: item.score,
              pack_width_in: item.pack_width_in,
              pack_height_in: item.pack_height_in,
            },
          });
        }
      }
    });

    // Group new items by row and update rowLayouts
    const itemsByRow: Record<number, LayoutItem[]> = {};
    newItems.forEach((item) => {
      const rowId = item.y + 1; // Convert y back to rowId
      if (!itemsByRow[rowId]) {
        itemsByRow[rowId] = [];
      }
      itemsByRow[rowId].push(item);
    });

    // Update each affected row
    if (Object.keys(itemsByRow).length > 0) {
      const updated = { ...rowLayouts };
      Object.entries(itemsByRow).forEach(([rowIdStr, items]) => {
        const rowId = parseInt(rowIdStr);
        const row = gridData.rows.find((r) => r.id === rowId);
        const existingRowLayout = updated[rowId] || row?.items || [];
        updated[rowId] = [...existingRowLayout, ...items];
      });
      setRowLayouts(updated);

      // Clear selections and close sidebar
      clearSelectedItems();
      closeAvailableProducts();
    }
  };

  const formatScore = (score: number) => {
    return (score * 100).toFixed(0);
  };

  const formatMargin = (margin: number) => {
    return (margin * 100).toFixed(1);
  };

  // Get available rows from gridData
  const availableRows =
    gridData?.rows.map((row) => ({
      id: row.id,
      name: row.name || `Shelf ${row.id}`,
    })) || [];

  // Calculate total selected count
  const totalSelected = Array.from(selectedAvailableItems.values()).reduce((sum, qty) => sum + qty, 0);

  return (
    <Card className={`h-screen fixed right-0 top-0 z-50 rounded-none border-l border-t-0 border-b-0 border-r-0 flex flex-col ${availableProductsSidebarExpanded ? 'w-3xl' : 'w-[32rem]'} transition-all duration-300`}>
      <CardHeader className='shrink-0 border-b'>
        <div className='flex items-center justify-between'>
          <CardTitle>Available Products</CardTitle>
          <div className='flex items-center gap-2'>
            <Button variant='ghost' size='icon' onClick={toggleAvailableProductsSidebarExpand} className='h-8 w-8 cursor-pointer' title={availableProductsSidebarExpanded ? 'Collapse' : 'Expand'}>
              {availableProductsSidebarExpanded ? <ChevronRight className='h-4 w-4' /> : <ChevronLeft className='h-4 w-4' />}
            </Button>
            <Button variant='ghost' size='icon' onClick={closeAvailableProducts} className='h-8 w-8 cursor-pointer' title='Close'>
              <X className='h-4 w-4' />
            </Button>
          </div>
        </div>
        <p className='text-sm text-muted-foreground mt-1'>
          {totalSelected} item{totalSelected !== 1 ? 's' : ''} selected
        </p>
      </CardHeader>
      <CardContent className='flex-1 overflow-y-auto p-0'>
        {loadingAvailableItems ? (
          <div className='flex items-center justify-center py-12'>
            <div className='animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600'></div>
          </div>
        ) : availableItems.length === 0 ? (
          <div className='p-4 text-center text-muted-foreground'>No products available</div>
        ) : (
          <div className='divide-y'>
            {availableItems.map((item) => {
              const quantity = selectedAvailableItems.get(item.id) || 0;
              return (
                <div key={item.id} className='p-4 hover:bg-accent transition-colors'>
                  <div className='flex items-start gap-3'>
                    <div className='flex-1 min-w-0'>
                      <div className='font-semibold text-sm mb-1'>{item.name}</div>
                      <div className='flex items-center gap-4 text-xs text-muted-foreground'>
                        <span>Score: {formatScore(item.score)}%</span>
                        <span>Margin: {formatMargin(item.margin)}%</span>
                        <span>Width: {item.pack_width_in ? `${item.pack_width_in.toFixed(1)}"` : 'N/A'}</span>
                      </div>
                    </div>
                    <div className='flex items-center gap-2 shrink-0'>
                      <Button size='icon' variant='outline' className='h-7 w-7' onClick={() => decrementItem(item.id)} disabled={quantity === 0}>
                        <Minus className='h-3 w-3' />
                      </Button>
                      <span className='w-8 text-center text-sm font-medium'>{quantity}</span>
                      <Button size='icon' variant='outline' className='h-7 w-7' onClick={() => incrementItem(item.id)}>
                        <Plus className='h-3 w-3' />
                      </Button>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        )}
      </CardContent>
      <div className='shrink-0 border-t p-4 space-y-3'>
        {/* Shelf Selector */}
        {availableRows.length > 0 && (
          <div>
            <Label className='text-xs text-muted-foreground mb-1 block'>Add to Shelf</Label>
            <Select value={targetRowId?.toString() || ''} onValueChange={(value) => setTargetRowId(parseInt(value))}>
              <SelectTrigger className='w-full'>
                <SelectValue placeholder='Select shelf' />
              </SelectTrigger>
              <SelectContent>
                {availableRows.map((row) => (
                  <SelectItem key={row.id} value={row.id.toString()}>
                    {row.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        )}
        <Button onClick={handleAddSelectedItems} disabled={totalSelected === 0 || loadingAvailableItems} className='w-full cursor-pointer'>
          Add Items ({totalSelected})
        </Button>
      </div>
    </Card>
  );
}

