'use client';

import { useState } from 'react';
import Link from 'next/link';
import { Label } from '@/components/ui/label';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Button } from '@/components/ui/button';
import { Checkbox } from '@/components/ui/checkbox';
import { Alert, AlertDescription } from '@/components/ui/alert';
import type { Planogram, PlanogramCategory } from '../../types';

interface PlanogramFormProps {
  planogram: Planogram;
  companyDisplays: Array<{ 
    id: number; 
    name: string; 
    display_category: string; 
    type: string; 
    width_in: string; 
    height_in: string; 
    depth_in: string;
  }>;
  standardDisplays: Array<{ 
    id: number; 
    name: string; 
    display_category: string; 
    type: string; 
    width_in: string; 
    height_in: string; 
    depth_in: string;
  }>;
  leafCategories: PlanogramCategory[];
  leafCategoriesLoading?: boolean;
  leafCategoriesError?: string | null;
  onRegenerate: (formData: { 
    name: string; 
    season: string; 
    shelfCount: number; 
    categoryIds: number[];
  }) => Promise<void>;
  isRegenerating: boolean;
  error?: Error | null;
}

export function PlanogramForm({
  planogram,
  companyDisplays,
  standardDisplays,
  leafCategories,
  leafCategoriesLoading = false,
  leafCategoriesError = null,
  onRegenerate,
  isRegenerating,
  error
}: PlanogramFormProps) {
  // Form state
  const [name, setName] = useState(planogram.name ?? '');
  const [season, setSeason] = useState(planogram.season ?? 'summer');
  const [shelfCount, setShelfCount] = useState(planogram.shelf_count ?? 1);
  const [selectedDisplay, setSelectedDisplay] = useState(planogram.display_id?.toString() ?? '');
  const [selectedCategoryIds, setSelectedCategoryIds] = useState<number[]>(planogram.category_ids ?? []);

  const handleToggleCategory = (categoryId: number) => {
    setSelectedCategoryIds(prev =>
      prev.includes(categoryId)
        ? prev.filter(id => id !== categoryId)
        : [...prev, categoryId]
    );
  };

  const handleRegenerate = async () => {
    await onRegenerate({
      name,
      season,
      shelfCount,
      categoryIds: selectedCategoryIds,
    });
  };

  const handleDisplayChange = (displayId: string) => {
    setSelectedDisplay(displayId);
    // Note: Display cannot be changed after creation per API limitations
    // This updates the UI state but won't persist to the backend
  };

  const allDisplays = [...companyDisplays, ...standardDisplays].sort((a, b) => {
    if (a.display_category === 'custom' && b.display_category === 'standard') return -1;
    if (a.display_category === 'standard' && b.display_category === 'custom') return 1;
    return 0;
  });

  return (
    <div className='space-y-4'>
      {/* Top row: Name, Season, Shelf Count, Display */}
      <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-12 gap-4'>
        {/* Name Field */}
        <div className='lg:col-span-3 space-y-2'>
          <Label htmlFor='planogram-name'>Name</Label>
          <Input
            id='planogram-name'
            type='text'
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder='Enter planogram name'
          />
        </div>

        {/* Season Selector */}
        <div className='lg:col-span-2 space-y-2'>
          <Label>Season</Label>
          <Select value={season} onValueChange={setSeason}>
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value='spring'>Spring</SelectItem>
              <SelectItem value='summer'>Summer</SelectItem>
              <SelectItem value='fall'>Fall</SelectItem>
              <SelectItem value='winter'>Winter</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Shelf Count Select */}
        <div className='lg:col-span-2 space-y-2'>
          <Label>Shelves</Label>
          <Select 
            value={shelfCount.toString()} 
            onValueChange={(value) => setShelfCount(parseInt(value, 10))}
          >
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              {Array.from({ length: 10 }, (_, i) => i + 1).map((num) => (
                <SelectItem key={num} value={num.toString()}>
                  {num}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        {/* Display Selector */}
        {allDisplays.length > 0 && (
          <div className='lg:col-span-5 space-y-2'>
            <div className='flex items-center gap-12'>
              <Label>Display</Label>
              <Link href='/dashboard/displays/new' className='text-xs text-primary hover:underline'>
                Create Custom Display
              </Link>
            </div>
            <Select value={selectedDisplay} onValueChange={handleDisplayChange}>
              <SelectTrigger className='w-full'>
                <SelectValue placeholder='Select display' />
              </SelectTrigger>
              <SelectContent className='min-w-[20rem]'>
                {allDisplays.map((display) => (
                  <SelectItem key={display.id} value={display.id.toString()}>
                    [{display.display_category.charAt(0).toUpperCase() + display.display_category.slice(1)}] {display.name} ({display.type.replace(/_/g, ' ')}) - {display.width_in}&quot; × {display.height_in}&quot; × {display.depth_in}&quot;
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        )}
      </div>

      {/* Categories Section */}
      <div className='space-y-2'>
        <Label>Product Categories</Label>
        
        {leafCategoriesLoading && (
          <div className='py-4 text-sm text-muted-foreground'>Loading categories...</div>
        )}

        {leafCategoriesError && (
          <Alert variant='destructive'>
            <AlertDescription>{leafCategoriesError}</AlertDescription>
          </Alert>
        )}

        {!leafCategoriesLoading && !leafCategoriesError && leafCategories.length > 0 && (
          <div className='grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-3 p-4 border border-border rounded-lg'>
            {leafCategories.map((category) => {
              const isSelected = selectedCategoryIds.includes(category.id);
              const isOriginal = (planogram.categories as PlanogramCategory[] | undefined)?.some(
                (c) => c.id === category.id
              ) ?? false;

              return (
                <div key={category.id} className='flex items-center space-x-2'>
                  <Checkbox
                    id={`category-${category.id}`}
                    checked={isSelected}
                    onCheckedChange={() => handleToggleCategory(category.id)}
                  />
                  <label
                    htmlFor={`category-${category.id}`}
                    className={`text-sm cursor-pointer ${
                      isOriginal ? 'font-medium text-primary' : 'text-foreground'
                    }`}
                  >
                    {category.name}
                  </label>
                </div>
              );
            })}
          </div>
        )}
      </div>

      {/* Actions */}
      <div className='flex items-center gap-4 pt-4 border-t border-border'>
        <Button
          onClick={handleRegenerate}
          disabled={isRegenerating || !name.trim() || selectedCategoryIds.length === 0}
        >
          {isRegenerating ? 'Regenerating...' : 'Regenerate Planogram'}
        </Button>

        {error && (
          <Alert variant='destructive' className='flex-1'>
            <AlertDescription>{error.message || 'Failed to regenerate planogram'}</AlertDescription>
          </Alert>
        )}
      </div>
    </div>
  );
}

