'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import type { Planogram, GridResponse } from '../../types';
import toast from 'react-hot-toast';

interface PlanogramDownloadButtonProps {
  planogram: Planogram | null;
  gridData: GridResponse | null;
}

export function PlanogramDownloadButton({ planogram, gridData }: PlanogramDownloadButtonProps) {
  const [downloadLoading, setDownloadLoading] = useState(false);

  const handleDownload = async () => {
    if (!planogram) {
      toast.error('No planogram data available');
      return;
    }

    setDownloadLoading(true);

    try {
      const { generatePlanogramCSV } = await import('@/lib/planogramCSV');
      const csvContent = generatePlanogramCSV(planogram, gridData);

      const blob = new Blob([csvContent], { type: 'text/csv' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      const dateStr = new Date().toISOString().split('T')[0];
      a.download = `${planogram.name.replace(/[^a-z0-9]/gi, '_')}-${dateStr}.csv`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);

      toast.success('Planogram downloaded successfully');
    } catch {
      toast.error('Failed to download planogram');
    } finally {
      setDownloadLoading(false);
    }
  };

  return (
    <Button onClick={handleDownload} size='sm' variant='outline' className='border-primary text-primary hover:bg-primary hover:text-primary-foreground' disabled={downloadLoading}>
      {downloadLoading ? 'Preparing...' : 'Download'}
    </Button>
  );
}

