'use client';

import { useParams } from 'next/navigation';
import { usePlanogramStore, usePlanogramData, useUpdatePlanogramMutation } from '@/features/planogram';
import { Grid, ThreeJSView, ProductSidebar, AvailableProductsSidebar, PlanogramForm, PlanogramDeleteButton, PlanogramHeader } from '@/features/planogram/components';
import { Card, CardContent } from '@/components/ui/card';

function PlanogramDetailContent() {
  const params = useParams();
  const planogramSlug = params?.planogramSlug as string;

  // Get state from consolidated store
  const gridData = usePlanogramStore.use.gridData();
  const rowLayouts = usePlanogramStore.use.rowLayouts();
  const sidebarOpen = usePlanogramStore.use.sidebarOpen();
  const availableProductsSidebarOpen = usePlanogramStore.use.availableProductsSidebarOpen();

  // React Query data (single source of truth)
  const { planogramData, planogramLoading, companyDisplays, standardDisplays, leafCategories, leafCategoriesLoading, leafCategoriesError, refetchPlanogram } = usePlanogramData(planogramSlug);
  const updateMutation = useUpdatePlanogramMutation();

  // Handle regenerate - receives form data from child
  const handleRegenerate = async (formData: {
    name: string;
    season: string;
    shelfCount: number;
    categoryIds: number[];
  }) => {
    if (!planogramData?.planogram || !formData.name.trim()) {
      return;
    }

    const planogram = planogramData.planogram;

    try {
      await updateMutation.mutateAsync({
        slug: planogramSlug,
        name: formData.name.trim(),
        width_in: planogram.width_in,
        height_in: planogram.height_in,
        season: formData.season,
        shelf_count: formData.shelfCount,
        category_ids: formData.categoryIds,
        preserve_layout: false,
      });

      await refetchPlanogram();
    } catch {
      // Error handled by mutation
    }
  };

  // Loading state
  if (planogramLoading) {
    return (
      <div className='flex justify-center items-center py-12'>
        <div className='animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600'></div>
      </div>
    );
  }

  // Error state
  if (!planogramData) {
    return (
      <div className='bg-card rounded-lg border border-border p-6'>
        <p className='text-muted-foreground'>Planogram not found</p>
      </div>
    );
  }

  const planogram = planogramData.planogram;

  return (
    <div className='relative'>
      {/* Sidebar - Fixed, overlays navbar */}
      {sidebarOpen && <ProductSidebar />}

      {/* Available Products Sidebar - Fixed, on right side */}
      {availableProductsSidebarOpen && <AvailableProductsSidebar />}

      {/* Main Content - aligned with navbar */}
      <div className='transition-all duration-300'>
        <div className='max-w-7xl mx-auto px-6 md:px-0 py-8'>
          <PlanogramHeader planogram={planogram} />

          {/* Horizontal Form at top */}
          {planogram && (
            <Card className='mb-8'>
              <CardContent className='p-6'>
                <PlanogramForm
                  planogram={planogram}
                  companyDisplays={companyDisplays}
                  standardDisplays={standardDisplays}
                  leafCategories={leafCategories}
                  leafCategoriesLoading={leafCategoriesLoading}
                  leafCategoriesError={leafCategoriesError}
                  onRegenerate={handleRegenerate}
                  isRegenerating={updateMutation.isPending}
                  error={updateMutation.error}
                />
              </CardContent>
            </Card>
          )}

          <div className='space-y-6'>
            {gridData ? (
              <>
                <Grid />
                {Object.keys(rowLayouts).length > 0 && <ThreeJSView gridData={gridData} rowLayouts={rowLayouts} />}
              </>
            ) : (
              <Card>
                <CardContent className='p-6'>
                  <p className='text-muted-foreground'>No layout data available for this planogram.</p>
                </CardContent>
              </Card>
            )}
          </div>

          <PlanogramDeleteButton />
        </div>
      </div>
    </div>
  );
}

export default function PlanogramDetailPage() {
  return <PlanogramDetailContent />;
}
